<template>
    <div class="message-detail">
        <navigate title="详情" position="absolute"/>
        <div class="scroll-wrapper">
            <div class="header border-1px-b">
                <h2 class="title">{{detail.title}}</h2>
                <p class="time">{{detail.publishTime}}</p>
            </div>
            <div class="message-content" v-html="detail.content"></div>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {queryNoticeDetail} from "../../../api/personal"


    export default {
        name: "detail",
        data() {
            return {
                detail: {}
            }
        },
        props: {
            id: {
                type: String,
                default: ''
            }
        },
        created() {
            this.queryNoticeDetail()
        },
        methods: {
            queryNoticeDetail() {
                queryNoticeDetail({id: this.id}).then(res => {
                    this.detail = res
                })
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../../common/stylus/variable.styl"

    .message-detail
        padding-top 46px

        .scroll-wrapper
            width 100%
            height 100%
            overflow-x hidden
            overflow-y auto

            .header
                padding 10px 15px
                text-align center

                .title
                    line-height 2.33
                    font-size $font-size-lm

                .time
                    line-height 1.5
                    font-size $font-size-sm
                    opacity 0.7

            .message-content
                padding 10px 13px
                line-height 1.74625
                font-size 16px
                text-indent 2em
                text-align justify
                opacity 0.7
                p
                    text-indent 2em
                    line-height 1.74625
                img
                    display block
                    margin 10px 0
                    max-width 100%
</style>



